
.CheckBoxDescription {
    line-height: 1.4em;
    margin: 10px 0 30px;
    text-align: center;
    font-weight: 300;
    font-size: 20px;
}

@media (min-width: 320px) {
    .BoxContainer {
        cursor: pointer;
        margin: 0 auto;
        margin-top: 20px;
        text-align: center;
        max-width: 90%;
        flex-basis: 90%;
        display: flex;
        flex-direction: column;
    }

    .BoxOption {
        margin-bottom: 30px;
    }

    .SelectBox {
        width: 90%;
        margin: 0 auto;
        margin-left: 5px;
    }
}

@media (min-width: 600px) {
    .BoxContainer {
        cursor: pointer;
        margin: 0 auto;
        margin-top: 20px;
        text-align: center;
        max-width: 66.333333%;
        flex-basis: 66.333333%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .BoxOption {
        margin: 0;
    }

    .SelectBox {
        width: 18%;
        margin-left: auto;
        margin-right: 110px;
        margin-top: 30px;
        margin-bottom: 30px;
    }
}

.InputContainer {
    color: #999;
    width: 116px;
    height: 116px;
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 0;
    position: relative;
}

.InputContainer:hover {
    color: #f50057;
}

.InputOption {
    color: inherit;
    text-align: center;
    transition: all 0.2s;
    border-color: #ccc;
    border-style: solid;
    border-width: 4px;
    border-radius: 50%;
    height: 108px;
    vertical-align: middle;
    display: flex;
    align-items: inherit;
    justify-content: inherit;
    margin: 0 0 9px 0;
    position: relative;
    padding: 0;
}

.InputOption:hover {
    background-color: rgba(254, 0, 87, 0.1);
    border-color: #e91e63;
}

.InputOption:active + .icon {
    background-color: rgba(254, 0, 87, 0.1);
    border-color: #e91e63;
}

.IconOption-1 {
    font-size: 40px;
    line-height: 111px;
    font-weight: 900;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
}

.icon-1 {
    font-size: 40px;
    position: absolute;
    right: 32px;
    top: 32px;
}

.icon-2 {
    font-size: 40px;
    position: absolute;
    right: 35px;
    top: 33px;
}

.icon-3 {
    font-size: 40px;
    position: absolute;
    right: 37px;
    top: 36px;
}

.InputCheck {
    top: 0;
    left: 0;
    width: 113px;
    cursor: inherit;
    height: 113px;
    margin: 0;
    opacity: 0;
    padding: 0;
}

.LabelCheck {
    top: -4px;
    position: absolute;
    display: block;
    width: 111px;
    height: 113px;
    border: 4px solid #ccc;
    border-radius: 50%;
    transition: all 0.2s;
}
.InputCheck:hover + .LabelCheck {
    background-color: rgba(254, 0, 87, 0.1);
    border-color: #e91e63;
}

.InputCheck:checked + .LabelCheck {
    background-color: rgba(254, 0, 87, 0.1);
    border-color: #e91e63;
}

.InputCheck:checked + i {
     color: #e91e63 !important;
 }

.IconDescription {
    margin: 0 auto;
    text-transform: uppercase;

}
