.HeaderLink {
    color: #555555;
    border: 0;
    cursor: pointer;
    display: block;
    padding: 10px 15px;
    position: relative;
    font-size: 12px;
    width: 33%;
    text-align: center;
    /*transition: all .3s;*/
    font-weight: 500;
    line-height: 18px;
    border-radius: 30px;
    text-transform: uppercase;
    text-decoration: none;
}
.HeaderLink-active {
    background-color: #e91e63;
    color: #FFF;
    cursor: pointer;
    padding: 12px;
    font-size: 12px;
    text-align: center;
    font-weight: 500;
    border-radius: 4px;
    text-transform: uppercase;
}