.HeaderWrapper {
    padding: 25px 0 35px;
    text-align: center;
}

.HeaderTitle {
    margin: 0;
    font-size: 1.825em;
    line-height: 1.4em;
    font-weight: lighter
}
.HeaderDescription {
    margin: 5px 0 0;
    font-size: 1.25em;
    line-height: 1.4em;
}

.HeaderItem {
    list-style: none;
    display: flex;
    position: relative;
    text-align: center;
    margin-left: 0;
    flex-grow: 1;
    margin-top: 20px;
    padding-left: 0;
    margin-bottom: 0;
    background-color: rgba(200, 200, 200, 0.2);
    margin-bottom: 30px;
}

