.UserWrapper {
    padding: 20px 15px;
    margin-top: 20px;
    min-height: 340px;
}

.UserDescription {
    line-height: 1.4em;
    margin: 10px 0 30px;
    text-align: center;
    font-weight: 300;
    font-size: 20px;
}
@media (min-width: 320px) {
    .UserContainer {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        justify-content: space-between;
        margin-bottom: 15px;
    }
    .PictureContainer {
        width: 100%;
        position: relative;
        cursor: pointer;
        text-align: center;

    }
    .InputWrapper {
        width: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }

    .MailContainer {
        display: block;
        width:100%;
        margin: 0 auto;
        position: relative;
    }
}

@media (min-width: 700px) {

    .UserContainer {
        width: 70%;
        display: flex;
        margin: 0 auto;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 15px;
    }

    .PictureContainer {
        width: 33.33333%;
        position: relative;
        cursor: pointer;
        text-align: center;
        margin-right: 30px;
    }

    .InputWrapper {
        width: 66%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }

    .MailContainer {
        display: block;
        width: 71%;
        margin: 0 auto;
        position: relative;
    }
}

.Picture {
    width: 106px;
    height: 106px;
    background-color: #999;
    border: 4px solid #ccc;
    color: #fff;
    border-radius: 50%;
    margin: 5px auto;
    overflow: hidden;
    transition: all .2s;
}

.Picture:hover {
    border: 4px solid #e91e63;
    transition: all .5s;
}

.PictureSrc {
    width: 100%;
}

.InputSrc {
    cursor: pointer;
    display: block;
    height: 100%;
    left: 0;
    opacity: 0 !important;
    position: absolute;
    top: 0;
    width: 100%;
}

.PictureDescription {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: .8em;
    text-transform: uppercase;
    font-weight: 500;
}

.InputOption {
    margin: 0 0 17px 0;
    position: relative;
    padding-top: 40px;
    vertical-align: unset;
}

.LabelContainer {
    position: relative;
    margin-bottom: 30px;
}

.FirstIcon {
    position: absolute;
    right: 0;
    top: 18px;
    user-select: none;
}

.LastIcon {
    position: absolute;
    right: 0;
    top: 20px;
    user-select: none;
}

.MuiFormControl-root {
    width: 100%;
}

.MailIcon {
    position: absolute;
    right: 6px;
    top: 19px;
    user-select: none;
}
