body {
    background-color: #eee;
}
.first {
    flex-grow: 0;
    max-width: 66.666667%;
    flex-basis: 66.666667%;
    margin: 0 auto;
}

.main {
    font-family: Roboto, sans-serif;
    color: rgba(0, 0, 0, 0.87);
    width: 100%;
    margin: 25px 0;
    display: inline-block;
    position: relative;
    background: #FFF;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
    min-height: 410px;
    transition: all 300ms linear;
    border-radius: 6px;
    font-weight: lighter
}
